.section {
  width: 600px;
  min-height: calc(100vh - 368px);
  max-height: calc(100vh - 128px);
  padding-left: 16px;
  padding-right: 16px;
}

.wrapper {
  max-width: 584px;
  min-height: calc(100% - 64px);
  display: flex;
  flex-direction: column;
  margin-bottom: 40px;
  margin-right: -16px;
}

.list {
  list-style: none;
  margin-top: 0;
  margin-bottom: 16px;
  padding-left: 0;
  overflow: auto;
  min-height: 300px;
  max-height: calc(100vh - 450px);
}

.list::-webkit-scrollbar {
  width: 8px;
}

.list::-webkit-scrollbar-track {
  background: #2F2F37;
}

.list::-webkit-scrollbar-thumb {
  background-color: #8585AD;
  border: 3px solid #8585AD;
}

.list_item {
  width: 568px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.list_item:not(:last-child) {
  margin-bottom: 16px;
}

.bun {
  align-self: flex-end;
  padding-left: 32px;
  margin-right: 16px;
}

.price_wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

.instruction {
  text-align: center;
}

.price {
  margin-right: 40px;
  line-height: 36px;
}

.price svg {
  width: 36px;
  height: 36px;
}

.element div {
  min-width: 538px;
}

